import { Accordion, AccordionSummary, Typography, AccordionDetails } from "@mui/material";
import { styles } from "../styles";
import UnfoldMoreDoubleIcon from '@mui/icons-material/UnfoldMoreDouble';

export default function Expando({
    title,
    subtitle,
    body
}: {
    title: string;
    subtitle: string;
    body: string[];
}) {
    return (
        <Accordion>
            <AccordionSummary expandIcon={<UnfoldMoreDoubleIcon />} sx={styles.accordionHeader}>
                <Typography sx={styles.headerTitle}>{title}</Typography>
                <Typography sx={styles.headerSubtitle}>{subtitle}</Typography>
            </AccordionSummary>
            <AccordionDetails sx={styles.accordionBody}>
                {body.map((line, index) => (
                    <Typography key={index}>
                        {line}
                    </Typography>
                ))}
            </AccordionDetails>
        </Accordion>);
}